import React, { useRef } from "react";
import { ProfileData } from "@/app/utility/props";

interface Props {
  profile: ProfileData;
  setProfile: React.Dispatch<React.SetStateAction<ProfileData>>;
}

export default function ResumeUpload({ profile, setProfile }: Props) {
  const fileRef = useRef<HTMLInputElement | null>(null);

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setProfile({ ...profile, resume: file.name });
    }
  };

  return (
    <div>
      <h2 className="text-xl font-semibold text-gray-800 mb-4">Resume</h2>

      <div className="flex items-center justify-between border border-gray-300 bg-gray-50 rounded-lg p-4">
        <div className="flex flex-col">
          <span className="text-gray-700 font-medium">
            {profile.resume ? "Uploaded Resume:" : "No resume uploaded"}
          </span>
          {profile.resume && (
            <span className="text-sm text-gray-500 mt-1">{profile.resume}</span>
          )}
        </div>

        <button
          type="button"
          onClick={() => fileRef.current?.click()}
          className="bg-blue-600 text-white px-5 py-2.5 rounded-lg hover:bg-blue-700 transition-all"
        >
          {profile.resume ? "Replace Resume" : "Upload Resume"}
        </button>
      </div>

      <input
        ref={fileRef}
        type="file"
        accept=".pdf"
        className="hidden"
        onChange={handleFileChange}
      />

      <p className="text-sm text-gray-500 mt-2">
        Only PDF files are allowed (max size 5MB).
      </p>
    </div>
  );
}
