import { ProfileFormProps } from "@/app/utility/props";
import React from "react";


export default function ProfileForm({ profile, setProfile }: ProfileFormProps) {
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const inputClass =
    "border border-gray-300 rounded-lg px-4 py-2.5 w-full focus:outline-none focus:ring-2 focus:ring-blue-500 placeholder-gray-400 bg-gray-50 transition-all";

  return (
    <div>
      <h2 className="text-xl font-semibold text-gray-800 mb-4">Personal Details</h2>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
        <input
          type="text"
          name="firstName"
          placeholder="First Name"
          value={profile.firstName}
          onChange={handleChange}
          className={inputClass}
        />

        <input
          type="text"
          name="lastName"
          placeholder="Last Name"
          value={profile.lastName}
          onChange={handleChange}
          className={inputClass}
        />

        <input
          type="text"
          name="phone"
          placeholder="Phone Number"
          value={profile.phone}
          onChange={handleChange}
          className={inputClass}
        />

        <input
          type="email"
          name="email"
          placeholder="Email Address"
          value={profile.email}
          onChange={handleChange}
          className={inputClass}
        />

        <input
          type="password"
          name="password"
          placeholder="Password"
          value={profile.password}
          onChange={handleChange}
          className={inputClass}
        />

        <input
          type="password"
          name="confirmPassword"
          placeholder="Confirm Password"
          value={profile.confirmPassword}
          onChange={handleChange}
          className={inputClass}
        />
      </div>
    </div>
  );
}
