"use client";

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import ProfileForm from "./ProfileForm";
import SkillsInput from "./SkillsInput";
import ResumeUpload from "./UploadResume";
import { ProfileData } from "@/app/utility/props";
import { useUser, useUpdateUser } from "@/app/customHooks/useUser";
import { useGetMe } from "@/app/customHooks/useApi";

interface ProfilePageProps {
  id: string;
}

export default function ProfilePage({ id }: ProfilePageProps) {
  const router = useRouter();
  const { data: user, isLoading, error } = useGetMe();
  const updateUserMutation = useUpdateUser();

  const [profile, setProfile] = useState<ProfileData>({
    firstName: "",
    lastName: "",
    phone: "",
    email: "",
    password: "",
    confirmPassword: "",
    skills: [],
    resume: "",
  });

  useEffect(() => {
    if (user) {
      setProfile({
        firstName: user.firstName || "",
        lastName: user.lastName || "",
        phone: user.phone || "",
        email: user.email || "",
        password: "",
        confirmPassword: "",
        skills: user.skills || [],
        resume: user.resume || "",
      });
    }
  }, [user]);

  const handleSave = async () => {
    try {
      await updateUserMutation.mutateAsync({ id, data: profile });
      alert("Profile updated successfully!");
    } catch {
      alert("Failed to update profile");
    }
  };

  const handleLogout = () => {
    // Clear token or user data from storage
    localStorage.removeItem("token");
    localStorage.removeItem("user");

    // Redirect to login
    router.push("/login");
  };

  if (isLoading)
    return <p className="text-center mt-10">Loading profile...</p>;
  if (error)
    return (
      <p className="text-center mt-10 text-red-600">
        Failed to load user profile.
      </p>
    );

  return (
    <div className="min-h-screen bg-gray-50 py-10 px-4">
      <div className="max-w-4xl mx-auto bg-white rounded-2xl shadow-lg p-8 border border-gray-100 relative">
        {/* Logout Button */}
        <button
          onClick={handleLogout}
          className="absolute top-5 right-5 text-red-500 hover:text-red-700 font-semibold border border-red-200 px-4 py-1.5 rounded-md transition-all"
        >
          Logout
        </button>

        <h1 className="text-3xl font-bold text-gray-800 mb-2 text-center">
          My Profile
        </h1>
        <p className="text-gray-500 text-center mb-8">
          Update your personal details, skills, and resume.
        </p>

        <div className="space-y-8">
          <ProfileForm profile={profile} setProfile={setProfile} />
          <SkillsInput profile={profile} setProfile={setProfile} />
          <ResumeUpload profile={profile} setProfile={setProfile} />
        </div>

        <div className="text-center mt-10">
          <button
            onClick={handleSave}
            disabled={updateUserMutation.isPending}
            className="px-8 py-3 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-all shadow-sm hover:shadow-md disabled:opacity-70"
          >
            {updateUserMutation.isPending ? "Saving..." : "Save Changes"}
          </button>
        </div>
      </div>
    </div>
  );
}
